.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 
    0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 500px;
    font-weight: 300;
}

.about__container .idiom {
    display: block;
    font-size: 27px;
    font-weight: 350;
    font-style: italic;
    margin-bottom: 30px;
}

.about__container .intro {
    display: block;
    font-size: 20px;
}

.about__container .contact {
    display: block;
    margin-top: 10px;
}